// grabbing the html elements
const select = document.getElementById('breeds');
const card = document.querySelector('.card'); 
const form = document.querySelector('form');


module.exports = {

    generateList : (list) => {
        // I have used this way, but it could be like that bellow.
        // let html = '';
        // for(let breed in list.message){
        //     html += `<option value="${list.message[breed]}">${list.message[breed]}</option> `;
        // }
        // select.innerHTML = html;

        // Here is who it could be instead.
        const options = list.message.map( items => `
            <option value="${items}">${items}</option>
        `).join('');
        select.innerHTML = options;
    },

    generateImg : ( data ) =>  {
        const html = `
            <img src="${data.message}" alt="" />
            <p class="">Click to see image of ${select.value} </p>
        `
        card.innerHTML = html;
    },

    getFirstBreed: (list) => {
            for(let breed in list.message){
                const number1 = list.message[0];
                return  number1;
            }      
    },

    ChangeImgOnClick: (data) => {
        const val = select.value
        const imgSrc = card.querySelector('img');
        const p = card.querySelector('p');

        imgSrc.src = data.message;
        imgSrc.alt = val;
        p.textContent = `Click to view more of ${val}.`;


    }



}


