// import css
import '../css/main.sass';

// ------------------------------------------
//  POST DATA
// ------------------------------------------

// require the others js documents 
const Fetch = require('./fetchApi'); 


// bound a event listener to the select. 
const select = document.getElementById('breeds');
select.addEventListener('change', () => {

    const breed =  select.value; 
    const url = `https://dog.ceo/api/breed/${breed}/images/random`
    Fetch.toFetch(url);
});

// bund a event listener to card
const card = document.querySelector('.card');
card.addEventListener('click', () => { 
    const url = `https://dog.ceo/api/breed/${select.value}/images/random`
    Fetch.toFecthOnClick(url);
} );


    


