// ------------------------------------------
//  FETCH FUNCTIONS
// ------------------------------------------

// import the helper function 
const Helper = require('./helper'); 

module.exports = {

    toPopulateList : fetch('https://dog.ceo/api/breeds/list')
                    .then( response => response.json() )
                    .then( list => Helper.generateList(list) )
    ,

    toPopulateDiv : fetch('https://dog.ceo/api/breeds/list')
                     .then( response => response.json() )
                     .then( img => { 
                        const name = img.message[0];
                        fetch(`https://dog.ceo/api/breed/${name}/images/random`)
                        .then( response => response.json() )
                        .then( data => Helper.generateImg(data) );
                     })
    ,
    
    toFetch : ( url ) => { 
            return fetch(url)
            .then( response => response.json() )
            .then( data =>  { Helper.generateImg(data); } )
    },

    toFecthOnClick : (url) => {
        return fetch(url)
        .then( response => response.json() )
        .then( data => Helper.ChangeImgOnClick( data ) )
    }

}





